@import "./variables";

.container {
  display: flex;
  justify-content: center;
}
.home {
  color: $clr-neutral-100;
  display: flex;
  width: 350px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  h5 {
    font-family: $ffSecondary;
    font-size: 1em;
    font-weight: 400;
    text-transform: uppercase;
    margin: 0;
    letter-spacing: 3px;
    color: $clr-primary-100;
  }
  h1 {
    font-family: $ffPrimary;
    margin: 0;
    text-transform: uppercase;
    font-size: 80px;
    font-weight: 400;
  }
  p {
    font-family: $ffSecondary;
    font-size: 15px;
    line-height: 25px;
    font-weight: 400;
    text-align: center;
    width: 350px;
    color: $clr-primary-100;
  }
  button {
    margin-top: 20%;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    font-family: $ffPrimary;
    font-size: 20px;
    letter-spacing: 1.25px;
    text-transform: uppercase;
    border: 0;
    &:hover {
      box-shadow: 0 0 0 40px hsla(0, 0%, 100%, 0.3);
    }
  }
}
