@import "../variables";

.container {
  display: flex;
  justify-content: center;
}
.destination {
  color: $clr-neutral-100;
  display: flex;
  width: 350px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 16px;
  img {
    width: 170px;
    height: 170px;
  }
  h5 {
    display: flex;
    justify-content: space-between;
    letter-spacing: 3px;
    font-family: $ffSecondary;
    text-transform: uppercase;
    font-weight: normal;
    color: $clr-neutral-100;
    line-height: 19px;
    gap: 1em;
    span {
      font-weight: bold;
      opacity: 0.25;
    }
  }
  h1 { 
    font-family: $ffPrimary;
    font-weight: 400;
    font-size: 56px;
    line-height: 64px;
    color: $clr-neutral-100;
    text-transform: uppercase;
  }
  .description {
    color: $clr-primary-100;
    font-size: 15px;
    line-height: 25px;
    font-family: $ffThird;
    text-align: center;
    width: 322px;
  }
  .line {
    width: 100%;
    height: 1px;
    background-color: $clr-neutral-200;
  }
  .distance {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    margin-top: 3em;
    h5 {
      font-family: $ffSecondary;
      font-weight: 14px;
      line-height: 17px;
      text-transform: uppercase;
      letter-spacing: 2.3625px;
      margin: 0;
      font-weight: 400;
      color: $clr-primary-100;
    }
    p {
      font-family: $ffSecondary;
      font-size: 28px;
      line-height: 32px;
      font-weight: 400;
      text-transform: uppercase;
      color: $clr-neutral-100;
      margin: 0;
    }
  }
  .travelTime {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    margin-top: 3em;
    h5 {
      font-family: $ffSecondary;
      font-weight: 14px;
      line-height: 17px;
      text-transform: uppercase;
      letter-spacing: 2.3625px;
      margin-top: 3em;
      margin-bottom: 0;
      font-weight: 400;
      color: $clr-primary-100;
      margin: 0;
    }
    p {
      font-family: $ffSecondary;
      font-size: 28px;
      line-height: 32px;
      font-weight: 400;
      text-transform: uppercase;
      color: $clr-neutral-100;
      margin: 0;
      padding-bottom: 50px;
    }
  }
}
