@import '../variables';

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h5 {
    display: flex;
    justify-content: space-between;
    letter-spacing: 3px;
    font-family: $ffSecondary;
    text-transform: uppercase;
    font-weight: normal;
    color: $clr-neutral-100;
    line-height: 19px;
    gap: 1em;
    span {
      font-weight: bold;
      opacity: 0.25;
    }
  }
  .imageContainer {
    width: 80%;
    display: flex;
    justify-content: center;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: $clr-neutral-200;
    }
  }
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin-top: 1.5em;
    * {
      margin: 0.75em 0 0 0;
      font-weight: 400;
    }
    h3 {
      color: $clr-neutral-100;
      opacity: 0.2;
      font-size: 16px;
      font-family: $ffPrimary;
      text-transform: uppercase;
    }
    h1 {
      font-family: $ffPrimary;
      font-size: 24px;
      color: $clr-neutral-100;
      text-transform: uppercase;
    }
    p{
      color: $clr-primary-100;
      font-size: 15px;
      line-height: 25px;
      font-family: $ffThird;
      text-align: center;
    }
  }
}